const MiniFramework = {
	currentComponent: null, // Aktualny komponent, kt√≥ry jest renderowany
	stateIndex: 0, // Indeks stanu dla hook√≥w
	effectIndex: 0, // Indeks efektu dla hook√≥w
	stateMap: new WeakMap(), // Mapa przechowujƒÖca stany komponent√≥w
	effectMap: new WeakMap(), // Mapa przechowujƒÖca efekty komponent√≥w
	componentMap: new WeakMap(), // Mapa przechowujƒÖca komponenty i ich odpowiadajƒÖce im elementy DOM
	contextMap: new WeakMap(), // Mapy WeakMap do przechowywania warto≈õci kontekst√≥w

	// Funkcja do tworzenia elementu
	createElement: (tag, props, ...children) => {
		if (typeof tag === "function" && !tag.isReactComponent) {
			// Je≈ºeli tag jest funkcjƒÖ i nie jest komponentem klasowym
			return { tag, props: { ...props, children } };
		}

		if (tag.prototype && tag.isReactComponent) {
			// Je≈ºeli tag jest komponentem klasowym
			const componentInstance = new tag(props); // Tworzy instancjƒô komponentu
			componentInstance.willInit(); // Metoda wywo≈Çywana przed montowaniem komponentu
			const componentElement = componentInstance.mount(); // Montuje komponent
			componentInstance.didInit(); // Metoda wywo≈Çywana po zamontowaniu komponentu
			return componentElement;
		}

		// Zwraca obiekt reprezentujƒÖcy element DOM
		return {
			tag,
			props: { ...props, children },
		};
	},

	//! Skonczyƒá portale !!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!!
	// Funkcja tworzƒÖca portal
	createPortal: (frameworkEl, targetContainer) => {
		if (!targetContainer) {
			// Je≈õli kontener nie zosta≈Ç przekazany, domy≈õlnie renderujemy do "modal-root"
			targetContainer = document.getElementById("modal-root");
		}

		if (!targetContainer) {
			console.error("Target container for portal not found.");
			return null;
		}

		MiniFramework.render(frameworkEl, targetContainer, true);
		return null;
	},

	// Funkcja do renderowania elementu w kontenerze DOM
	render: function (frameworkEl, container, replace = false) {
		if (frameworkEl && frameworkEl.props?.isPortal) {
			console.log(typeof frameworkEl.tag);
			this.createPortal(frameworkEl.content, frameworkEl.targetContainer);
			return;
		}

		// Obs≈Çuga tablicy element√≥w
		if (Array.isArray(frameworkEl)) {
			if (replace) {
				container.innerHTML = ""; // Opr√≥≈ºnia kontener, je≈õli replace jest true
			}
			frameworkEl.forEach((element) => {
				this.render(element, container, false); // Rekurencyjnie renderuje ka≈ºdy element z tablicy
			});
			return;
		}

		// Sprawdzamy, czy to jest portal
		if (frameworkEl?.props?.isPortal) {
			this.createPortal(frameworkEl.content, frameworkEl.targetContainer);
			return;
		}

		// Obs≈Çuga string√≥w i liczb
		if (typeof frameworkEl === "string" || typeof frameworkEl === "number") {
			if (replace) {
				container.innerHTML = ""; // Opr√≥≈ºnia kontener, je≈õli replace jest true
			}
			container.appendChild(document.createTextNode(frameworkEl)); // Dodaje tekst do kontenera
			return;
		}

		// Obs≈Çuga komponent√≥w funkcyjnych
		if (frameworkEl && typeof frameworkEl.tag === "function") {
			this.currentComponent = frameworkEl; // Ustawia aktualny komponent
			this.stateIndex = 0; // Resetuje indeks stanu
			this.effectIndex = 0; // Resetuje indeks efektu
			const componentElement = frameworkEl.tag(frameworkEl.props); // Wywo≈Çuje funkcjƒô komponentu
			this.currentComponent = null;

			const domNode = this.render(componentElement, container, replace); // Rekurencyjnie renderuje element
			this.componentMap.set(frameworkEl, domNode); // Mapuje komponent na element DOM
			return domNode;
		}

		if (replace && container.firstChild) {
			const oldComponent = this.componentMap.get(container.firstChild);
			if (oldComponent) {
				this.cleanupEffects(oldComponent); // Czyszczenie efekt√≥w, gdy komponent zostaje zastƒÖpiony
			}
		}

		// Tworzy rzeczywisty element DOM dla standardowych tag√≥w
		const actualDOMElement = document.createElement(frameworkEl?.tag);

		if (frameworkEl?.props && frameworkEl?.props.className) {
			actualDOMElement.className = frameworkEl.props.className; // Zastosowanie klasy CSS
		}
		// Aplikuje atrybuty (props) do utworzonego elementu, z wy≈ÇƒÖczeniem dzieci
		Object.keys(frameworkEl?.props || {})
			.filter((key) => key !== "children")
			.forEach((property) => {
				if (property.startsWith("on")) {
					// Dodaje event listener, je≈õli atrybut zaczyna siƒô od "on"
					actualDOMElement.addEventListener(
						property.substring(2).toLowerCase(),
						frameworkEl.props[property]
					);
				} else if (property === "className") {
					// Obs≈Çuga klasy CSS
					actualDOMElement.className = frameworkEl.props[property];
				} else {
					// Inne atrybuty
					actualDOMElement[property] = frameworkEl.props[property];
				}
			});

		// Rekurencyjnie renderuje dzieci
		frameworkEl?.props?.children?.forEach((child) => {
			this.render(child, actualDOMElement);
		});

		// Zastƒôpuje zawarto≈õƒá kontenera, je≈õli replace jest true
		if (replace) {
			container.innerHTML = "";
		}
		container.appendChild(actualDOMElement);

		// Uruchamia efekty po renderowaniu
		this.runEffects(frameworkEl);

		return actualDOMElement; // Zwraca element DOM
	},

	// Hook useState
	useState: function (initialState) {
		const component = this.currentComponent; // Pobiera aktualny komponent

		if (!component) {
			throw new Error("useState must be called within a component"); // B≈ÇƒÖd, je≈õli useState jest u≈ºywany poza komponentem
		}

		const stateIndex = this.stateIndex++; // Inkrementuje indeks stanu
		let componentState = this.stateMap.get(component) || []; // Pobiera stan komponentu

		if (!componentState[stateIndex]) {
			componentState[stateIndex] = initialState; // Inicjalizuje stan, je≈õli nie jest jeszcze ustawiony
		}

		const setState = (newState) => {
			const currentState = componentState[stateIndex]; // Bie≈ºƒÖcy stan
			const updatedState =
				typeof newState === "function" ? newState(currentState) : newState; // Nowy stan

			if (updatedState !== currentState) {
				componentState[stateIndex] = updatedState; // Aktualizuje stan, je≈õli siƒô zmieni≈Ç
				MiniFramework.update(component); // Aktualizuje komponent
			}
		};

		this.stateMap.set(component, componentState); // Aktualizuje mapƒô stan√≥w

		return [componentState[stateIndex], setState]; // Zwraca stan i funkcjƒô do jego aktualizacji
	},

	// Hook useEffect
	useEffect: function (effect, deps) {
		const component = this.currentComponent; // Pobieramy aktualny komponent

		if (!component) {
			throw new Error("useEffect must be called within a component");
		}

		const effectIndex = this.effectIndex++; // Pobieramy bie≈ºƒÖcy indeks efektu
		let componentEffects = this.effectMap.get(component) || [];

		const prevEffect = componentEffects[effectIndex];

		// Sprawdzamy, czy zale≈ºno≈õci uleg≈Çy zmianie
		const hasChanged =
			!prevEffect || !deps || deps.some((dep, i) => dep !== prevEffect.deps[i]);

		if (hasChanged) {
			if (prevEffect && prevEffect.cleanup) {
				prevEffect.cleanup(); // Czyszczenie poprzedniego efektu, je≈õli istnieje
			}

			const cleanup = effect(); // Wywo≈Çanie efektu i przechwycenie funkcji czyszczƒÖcej
			componentEffects[effectIndex] = { deps, cleanup };
		}

		this.effectMap.set(component, componentEffects); // Zapis efekt√≥w w mapie
	},

	// Uruchamianie efekt√≥w po renderowaniu komponentu
	runEffects: function (component) {
		const componentEffects = this.effectMap.get(component) || []; // Pobiera efekty komponentu
		componentEffects.forEach((effect) => {
			if (effect.cleanup) {
				effect.cleanup(); // Wywo≈Çuje funkcjƒô czyszczƒÖcƒÖ efektu, je≈õli istnieje
			}
			effect.cleanup = effect.effect(); // Uruchamia efekt i zapisuje funkcjƒô czyszczƒÖcƒÖ
		});
	},

	// Obs≈Çuga przerywania efekt√≥w podczas odmontowywania komponentu
	cleanupEffects: function (component) {
		const componentEffects = this.effectMap.get(component) || [];
		componentEffects.forEach((effect) => {
			if (effect.cleanup) {
				effect.cleanup(); // Wywo≈Çanie wszystkich funkcji czyszczƒÖcych
			}
		});
		this.effectMap.delete(component); // Usuniƒôcie efekt√≥w z mapy po odmontowaniu
	},

	diff: function (oldNode, newNode) {
		if (!oldNode || !newNode) return false; // Sprawdzenie, czy kt√≥ry≈õ z wƒôz≈Ç√≥w jest null

		if (typeof oldNode !== typeof newNode) return false;
		if (typeof newNode === "string" || typeof newNode === "number") {
			return oldNode === newNode;
		}
		if (oldNode.tag !== newNode.tag) {
			return false;
		}

		const oldProps = oldNode.props || {};
		const newProps = newNode.props || {};

		const oldKeys = Object.keys(oldProps);
		const newKeys = Object.keys(newProps);

		if (oldKeys.length !== newKeys.length) {
			return false;
		}

		for (let key of newKeys) {
			if (key === "children") continue;
			if (oldProps[key] !== newProps[key]) return false;
		}

		return true;
	},

	// Aktualizacja komponentu
	update: function (component) {
		const oldNode = this.componentMap.get(component);
		this.currentComponent = component; // Ustawiamy `currentComponent`, ≈ºeby dzia≈Ça≈Çy hooki
		const newNode = component.tag(component.props); // Tworzymy nowy element komponentu

		if (component?.props?.isPortal) {
			this.createPortal(
				component.props.content,
				component.props.targetContainer
			);
		}

		if (!this.diff(oldNode, newNode)) {
			this.cleanupEffects(component); // Czy≈õcimy efekty przed ponownym renderowaniem
			const domNode = this.render(component, oldNode, true);
			this.componentMap.set(component, domNode);
		} else {
			this.runEffects(component);
		}
		this.currentComponent = null; // Czyszczenie `currentComponent` po aktualizacji
	},

	// Funkcja do tworzenia kontekstu
	createContext: function (defaultValue) {
		const context = {
			defaultValue, // Warto≈õƒá domy≈õlna kontekstu
			state: defaultValue, // Bie≈ºƒÖcy stan kontekstu
			subscribers: new Set(), // Zbi√≥r subskrybent√≥w aktualizacji kontekstu
		};

		// Funkcja do modyfikacji warto≈õci kontekstu
		function setContextValue(newValue) {
			context.state = newValue; // Ustawia nowƒÖ warto≈õƒá stanu kontekstu
			context.subscribers.forEach((callback) => callback(newValue)); // Powiadamia subskrybent√≥w o zmianie
		}

		// Komponent Provider umo≈ºliwiajƒÖcy aktualizacjƒô kontekstu
		function Provider({ value, children }) {
			if (value !== undefined) {
				setContextValue(value); // Aktualizuje warto≈õƒá kontekstu, je≈õli zosta≈Ça podana
			}
			return children; // Renderuje dzieci
		}

		// Hook useContext do uzyskiwania warto≈õci kontekstu
		function useContext() {
			const [value, setValue] = MiniFramework.useState(context.state); // Uzyskuje aktualnƒÖ warto≈õƒá kontekstu

			MiniFramework.useEffect(() => {
				const updateValue = (newValue) => setValue(newValue); // Funkcja aktualizujƒÖca stan z nowƒÖ warto≈õciƒÖ kontekstu
				context.subscribers.add(updateValue); // Dodaje funkcjƒô aktualizujƒÖcƒÖ do subskrybent√≥w
				return () => context.subscribers.delete(updateValue); // Usuwa funkcjƒô z subskrybent√≥w podczas odmontowania
			}, []);

			return value; // Zwraca warto≈õƒá kontekstu
		}

		return {
			Provider, // Komponent Provider
			useContext, // Hook useContext
			setContextValue, // Funkcja do zmiany warto≈õci kontekstu
		};
	},
	useStyle: function (styles) {
		const component = this.currentComponent;
		if (!component) {
			throw new Error("useStyle must be called within a component");
		}

		const styleTagId = `style-${component.tag.name}`;
		let styleTag = document.getElementById(styleTagId);

		if (!styleTag) {
			styleTag = document.createElement("style");
			styleTag.id = styleTagId;
			document.head.appendChild(styleTag);
		}

		const className = `class-${component.tag.name}-${Math.random()
			.toString(36)
			.substr(2, 5)}`;

		// Konwersja styl√≥w z camelCase na kebab-case
		const css = `
			.${className} {
				${Object.entries(styles)
					.map(([key, value]) => {
						const kebabKey = key
							.replace(/([a-z])([A-Z])/g, "$1-$2")
							.toLowerCase();
						return `${kebabKey}: ${value};`;
					})
					.join(" ")}
			}
		`;

		// Dodawanie nowego CSS do istniejƒÖcego styleTag
		styleTag.appendChild(document.createTextNode(css));

		return className;
	},
};

// Klasa bazowa dla komponent√≥w
class MiniComponent {
	constructor(props) {
		this.props = props; // Przypisanie w≈Ça≈õciwo≈õci (props) do instancji komponentu
		this.state = {}; // Inicjalizacja stanu komponentu jako pustego obiektu
		this.willInit(); // Wywo≈Çanie metody willInit (przed montowaniem komponentu)
		this.mount(); // Wywo≈Çanie metody mount (montowanie komponentu)
		this.didInit(); // Wywo≈Çanie metody didInit (po zamontowaniu komponentu)
	}

	willInit() {} // Metoda wywo≈Çywana przed montowaniem komponentu

	didInit() {} // Metoda wywo≈Çywana po zamontowaniu komponentu

	didUpdate() {} // Metoda wywo≈Çywana po aktualizacji komponentu

	mainDiv() {
		this.name = this.constructor.name; // Przypisuje nazwƒô klasy do zmiennej name
		return `${this.constructor.name}`; // Zwraca nazwƒô klasy jako string
	}

	// Ustawia nowy stan komponentu
	setState(partialState) {
		this.state = { ...this.state, ...partialState }; // Aktualizuje stan komponentu
		MiniFramework.update(this); // Aktualizuje komponent w DOM
	}

	// Metoda montujƒÖca, musi byƒá zaimplementowana przez podklasƒô
	mount() {
		throw new Error("Component subclass must implement mount method."); // Rzuca b≈ÇƒÖd, je≈õli metoda nie zosta≈Ça zaimplementowana
	}

	// Oznacza, ≈ºe jest to komponent kompatybilny z Reactem
	static isReactComponent = true;
}

// Dodanie klasy MiniComponent do MiniFramework jako jego sk≈Çadnik
MiniFramework.Component = MiniComponent;

export default MiniFramework;
